import React from 'react'
import { Link } from "react-router-dom";
import '../../CSS/Navs.scss'
import styled from 'styled-components'

export const Navs = () => {
  return (
    <div>
       <HEADER>
        <nav className="minutes-page-navbars relative"
        style={{
        background: ' #FFFCA8'}}
        >
          <div className="left-sections">
            <span  className='me-2 text-dark '>Any issues?...</span>
            <Link to = '/Minutes' className='mx-3 button-left text-decoration-none text-dark buttons shadow-none'>Feedback</Link>
          </div>
          <div className="right-sections">
            <span className='me-2 text-dark'>Done ?</span>
            <Link to='/' target={'_parent'} className='mx-3 button-right text-decoration-none border-none shadow-none text-dark buttons' >Logout</Link>
          </div>
        </nav>
      </HEADER>
      {    <hr className="horrizontal relative top-0" 
        style={{
        border: '1px solid rgba(98, 97, 97, 0.9)', 
        margin:'5rem 0'
      }}
     />}


    </div>
  )
}
export default Navs

const HEADER = styled.div`
background: #FFFCA8 !important;
@media(max-width:786px){
  .button-right,
.button-left{
  position:relative;
  left:-1rem !important;
  font-size:1.5rem !important;
  color: #131313;
  font-weight:bold;
background: #FFFCA8;
text-shadow: #FFFCA8 2px 2px 0px, #9C9C9C 4px 4px 0px;
 
}
}
`


