
import React from 'react';
import '../../CSS/Minutes.scss';
import MinutesMenu from '../MinutesPage/MinutesMenu'



const meetings = [
  { id: 1, date: 'ICANW Constitution', url: 'MinutesPages/Constitution'},
  { id: 2, date: 'Christmas Contributions', url: 'MinutesPages/Update' },
  { id: 3, date: 'Feb-2023-02-05 Meeting', url: 'MinutesPages/Feb2023Meeting'},
  { id: 4, date: 'Feb-2022-02-13 Meeting', url: 'MinutesPages/Feb2022Meeting' },
  { id: 5, date: 'Mar-2022-03-13 Meeting' , url: 'MinutesPages/March2022Meeting' },
  { id: 6, date: 'April-2022-04-10 Meeting', url: 'MinutesPages/April2022Meeting' },
  { id: 7, date: 'May-2022-05-08 Meeting', url: 'MinutesPages/May2022meeting' },
  { id: 8, date: 'June-2022-06-12 Meeting', url: 'MinutesPages/June2022Meeting' },
  { id: 9, date: 'Sept-2022-09-18 Meeting', url: 'MinutesPages/Sept2022Meeting' },
  { id: 10, date: 'Oct-2022-09-10 Meeting', url: 'MinutesPages//Oct2022Meeting' },
  { id: 11, date: 'Nov-2022-19-11 Meeting', url: 'MinutesPages//Nov2022Meeting' },
  { id: 12, date: 'Oct-2019-02-13 Meeting', url: 'MinutesPages/Oct2019Meeting' },

];



function MeetingButton({ date, url }) {
  
  return (
    <a className="meeting-button" href={url}>
      {date}
    </a>
  );
} 

function Prev() {
  
  return (
    <>
    {<MinutesMenu/>}
    <div className="app relative top-0" style={{marginTop:'-2rem'}}>
      <h1 className="header">Previous Meeting Minutes</h1>
      <div className="meeting-buttons">
        {meetings.map((meeting) => (
          <MeetingButton key={meeting.id} date={meeting.date} url={meeting.url}/>
        ))}
      </div>
    </div>
    </>
  );
}

export default Prev;
