import React from 'react'
import styled from 'styled-components'
import logo from '../../image/LOGO.png'
import men from '../../image/menG.png';
import '../../../CSS/HeroImage.scss'
import bgImage from '../../image/imageajepg.jpeg'


const HeroImage = () => {
  return (
  
    <HEROIMAGE className="hero-image-cover relative top-10"
     >
    <img className='img-fluid bg-img'  src={bgImage} alt='hero background image'/>

<div className= " main-card position-absolute" style={{width: "30rem"}}>
  <div className="hero-card-body relative">
    <h6 className="card-title text-white">  
      <span className='card-title-span'>
        <span className='welcome-title'>Welcome <span className='To'>to</span>
        </span> <br/>
        <span className='welcome-title2'>
          Igbo Cultural <span  className='assoc'>Association</span></span>
          <br/>
         <span>North-west Ireland</span>
         </span>
    </h6>
    <p className="card-texts">
      Founded in 2010 with the shared goal of 
      promoting peace and unity through cultural integration,
      participating and volunteering
    </p><br/>
    <span className='motto'>
      Motto: With God Victory is sure
      </span> 
    <a className=" btn rounded-lg btn-customised button shadow-none outline-none pb-4" 
    >
    
      <i><span className='developer'>
      <br/>
        <span className='designer'>
        About the Website<br/>
         <span className='author'>
          Designed and developed by
          DinoTech<br/> <a className='a-btn relative top-2' herf = ''>Readmore</a>
          </span>
         </span>
      </span></i>
    </a>
  </div>
  <div className="overlays position-absolute" ></div>
</div>

{/* DROPDOWN LINK/IMAGES CONTENTS*/}


   <div className=' hero-image-inner-item relative top-5'>
      <div className='item d-flex relative'>
      <img className='max-w-48 max-h-48 relative top-10 ogene men'
        alt="Image" href="#home"
        src={men}
    />
      </div>
      <div className='item d-flex relative'>

      <img className='max-w-42 absolute max-h-36 rounded-circle hero-logo'
        alt="Image" href="#home"
        src={logo}
    />
      </div>
</div>

    </HEROIMAGE>
   
  )
}

export default HeroImage
const HEROIMAGE = styled.div`
position:relative;
@media(max-width:1920px){
.main-card{
  background:transparent !important;
  left:2rem;
  top:4.5rem !important;
  font-family: verdana;
  z-index: 10;
  border:none;
  .hero-card-body{  
    position:relative;
    left:1.5rem !important;
    z-index:2000 !important;
 .sp{font-size: 1.6rem;}
  @media(max-width:786px){
   border-left:4px solid purple !important;
   border-top-left-radius:10%;
   border-bottom-left-radius:2%;
   padding-left:5px !important;

}
@media(max-width:550px) and (min-width:320px){
        left:1rem !important;
        max-width:320px !important;
        .card-title-span{
          max-width:250px !important;
          min-width:250px !important;
   
        }
      }
      .card-title-span{
    color:aliceblue !important;
    text-shadow: offset-x offset-y blur-radius color;
    text-shadow: 5px 5px 5px rgb(5, 4, 4);
    @media(max-width:786px){
      max-height:3rem !important;
      min-height:4rem !important;
      padding:0 !important;
    
 }
  }
 
    .welcome-title{
      color:aliceblue !important;
      font-size: 4rem;
      text-shadow: offset-x offset-y blur-radius color;
      text-shadow: 5px 5px 5px rgb(5, 4, 4);
      -webkit-text-shadow: offset-x offset-y blur-radius color;
      -moz-text-shadow: 5px 5px 5px rgb(5, 4, 4);
      .To{ font-size: 2rem;}
      }

    }

  @media(max-width:992px){
    left:1.8rem !important;
  }
  @media(max-width:600px){
    left:1.5rem !important;
  }
      
 }
}


  .card-title{
    color:azure !important;
    font-size:1.5rem;
    font-weight:900;
    font-family: verdana;
    line-height:3rem;
    z-index:25;
  }
  .card-texts{
  padding-top:1rem;
  text-align:left;
  font-size: 1.2rem;
  font-weight:700;
  color:chartreuse !important;
  letter-spacing:1px;
  text-shadow: 2px 2px 2px rgba(0,0,0,0.7);
  font-family: harlow italic;
  @media(max-width:992px){
    max-width:340px !important;
    min-width:340px !important;
    text-shadow: 2px 2px 0 #201e1e, 4px 4px 0 #252424;
    text-align: left !important;
  }
  @media(max-width:550px){
       max-width:320px !important;
      }

 }
.button{
  display:flex;
  flex-wrap:wrap;
  flex-direction:column;
  min-height:100px important;
  justify-content:center;
  align-items:center;
  z-index:25 !important;
  padding: 2px 14px;
  border-top-left-radius:20%;
  border-bottom-right-radius:20%;
  background: linear-gradient(217deg, rgba(255,0,0,.8), rgba(255,0,0,0) 70.71%),    
  linear-gradient(127deg, rgba(0,255,0,.8), rgba(0,255,0,0) 70.71%),           
  linear-gradient(336deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%) !important;

 }
.overlays{
  width:100%;
  height:100%;
  left:0;
  right:0;
  top:0;
  bottom:0;
  background:black;
  opacity:0.1;
  z-index:-1;
  border:none;
  @media(max-width:786px){
  opacity:0.4;
  min-width:100vw !important;
  
 }
 }
@media(max-width:1200px){
margin:0 !important;
padding:0 !important;

}


   @media(max-width:786px){
    margin-left:-2rem!important;
    top:5.5rem !important;
    .card-title{
    line-height:2.5rem;
    font-size:1.8rem;   
     margin-top:-3rem !important;
     min-width:80vw !important;
    }
    .welcome-title{
    display:none !important;    
    }
    .welcome-title2{
      font-size:3rem;   
    }
    .assoc{
      font-size:2.5rem !important;
      margin-top:1rem !important;
    }
    .card-texts{
      font-size:1.2rem !important;
    
    }
    font-size:1.5rem;

    .button{
    margin-top:-1rem !important;
    padding:0 14px !important;
    background:#4d1b50;
    font-size:1.1rem !important;
  
  }
 
  }
  @media only screen and 
  (max-width: 600px) and 
  (min-width: 320px)  {
   left:1.8% !important;
   min-width:100%;
   padding:1rem 2rem !important;

 .card-texts{
  padding-right:1.5rem 
  !important;
  }
   }
  
`




 